.About {
    display: flex;
    flex-direction: column;
    flex: 1;
    text-align: center;
    background: linear-gradient(270deg, rgba(73, 11, 102, 1) 31%, rgba(50, 58, 194, 1) 93%);
    padding: 40px;
    font-family: "Kavoon", cursive;
    letter-spacing: 3px;
    .aboutHello {
        width: 100%;
        margin-inline: auto;
        margin-block: -200px;
        @media (max-width: 2250px) and (min-width: 1900px) {
            margin: auto;
            margin-block: -100px;
            width: 90%;
        }
        @media (max-width: 1900px) and (min-width: 1500px) {
            margin: auto;
            margin-top: -80px;
            width: 90%;
        }
        @media (max-width: 1500px) and (min-width: 1100px) {
            margin-block: -80px;
            width: 100%;
            height: 60%;
        }
        @media (max-width: 1100px) and (min-width: 800px) {
            margin-block: -80px;
            margin-top: -40px;
            width: 100%;
            height: 40%;
        }
    }
    .imKamil {
        margin-top: -210px;
        width: 100%;
        height: 100%;
        iframe {
            overflow: hidden;
            border: none;
        }
        @media (max-width: 2250px) and (min-width: 1900px) {
            margin: auto;
            margin-top: -180px;
            width: 90%;
            height: 90%;
        }
        @media (max-width: 1900px) and (min-width: 1500px) {
            margin: auto;
            margin-top: -230px;
            width: 90%;
            height: 60%;
        }
        @media (max-width: 1500px) and (min-width: 1100px) {
            margin: auto;
            margin-top: -120px;
            width: 100%;
            height: 60%;
        }
        @media (max-width: 1100px) and (min-width: 800px) {
            margin: auto;
            margin-top: -40px;
            width: 100%;
            height: 40%;
        }
    }
    .livingInGdansk {
        margin: auto;
        margin-top: -210px;
        width: 90%;
        height: 90%;
        iframe {
            overflow: hidden;
            border: none;
        }
        @media (max-width: 2250px) and (min-width: 1900px) {
            margin: auto;
            margin-top: -210px;
            width: 100%;
            height: 80%;
        }
        @media (max-width: 1900px) and (min-width: 1500px) {
            margin: auto;
            margin-top: -170px;
            width: 90%;
            height: 50%;
        }
        @media (max-width: 1500px) and (min-width: 1100px) {
            margin: auto;
            margin-top: -110px;
            width: 100%;
            height: 60%;
        }
        @media (max-width: 1100px) and (min-width: 800px) {
            margin: auto;
            margin-top: -180px;
            width: 100%;
            height: 40%;
        }
    }
    p {
        padding: 40px;
        font-size: 26px;
        margin: 0;
        padding: 0;
        color: #fefedf;
        padding: 25px;
    }
    p:nth-of-type(1) {
        font-size: 100px;
    }
    p:nth-of-type(2) {
        font-size: 75px;
    }
    p:nth-of-type(3) {
        font-size: 50px;
    }
    p:nth-of-type(4) {
        font-size: 25px;
    }
}
.media-container {
    border: none;
}
.mirror {
    width: 100%;
    height: 100%;
    position: absolute;
}
